/****************************************
Basic HTML element Overrides 
****************************************/
html,
body {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}
H2,
H3 {
  margin: 0;
}
/****************************************
CSS Variables
****************************************/
:root {
  --Padding: 10px;
  --Margin: var(--Padding);
  font-size: 16px;
}
/****************************************
Main App Container
****************************************/
#App {
  font-family: sans-serif;
  box-sizing: border-box;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background-color: #0057b8;
  color: white;
}
#App * {
  box-sizing: border-box;
}

/****************************************
HUD & Overlay 
****************************************/
.HUD {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.HUD > section {
  min-height: 100%;
  width: 100%;
  display: grid;
  justify-items: center;
  align-items: center;
}
.HUD > section > div {
  border: 1px solid black;
  text-align: center;
  padding: var(--Padding);
}
.HUD H2 {
  /*background-color: rgba(255, 255, 255, 0.5);*/
}
.HUD button,
.HUD input,
.HUD label {
  pointer-events: initial;
  cursor: pointer;
}
.HUD button {
  margin: var(--Padding) auto;
}
.HUD button:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.HUD ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
/**************************************
SplashScreen
**************************************/
.SplashScreen section,
.FaceScan section {
  background-color: #009fdb;
}
/**************************************
InformationForm
**************************************/
.InformationForm li {
  margin: var(--Padding) auto;
}
/**************************************
Custom Section
**************************************/
.ModelEditor > section {
  align-items: end;
}
.ModelEditor > section > div {
  max-width: 400px;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
}
.ModelEditor ul {
  padding: 0;
  margin: auto;
  transition: transform 1s;
}
.ModelEditor nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: auto 10px;
  margin-top: 10px;
}
.ModelEditor nav h3 {
  cursor: pointer;
  pointer-events: initial;
}
.ModelEditor nav h3:first-child {
  justify-self: start;
}
.ModelEditor nav h3:last-child {
  justify-self: end;
}
.CustomHolder {
  display: grid;
  pointer-events: initial;
  grid-template-columns: 100% 100% 100% 100%;
}

.CustomHolder > li {
  display: grid;
  gap: 15px;
  grid-template-columns: auto;
}
.CustomHolder ul:not(.colorSearch, .swatches) {
  display: inline-grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}
.colorSearch {
  display: inline-grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.swatches {
  display: inline-grid;
  gap: 10px;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
}
.swatches > li {
  height: 30px;
  width: 30px;
  border-radius: 50px;
  border: 1px solid gray;
  pointer-events: initial;
  cursor: pointer;
}
/* .CustomHolder .colorSearch > li:first-of-type {
  grid-column: 1 / -1;
} */
.CustomHolder img {
  cursor: pointer;
  border: 1px solid gray;
}
.CustomHolder img.active {
  border: 1px solid red;
}
.CustomHolder li.active {
  border: 1px solid red;
}
/**************************************
QR Scan
**************************************/
.ScanFriends ul:not(.ScanFriend) {
  display: grid;
  gap: 15px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 10px;
}
.ScanFriends ul:not(.ScanFriend) > li {
  border: 1px solid black;
  padding: 10px;
  border-radius: 15px;
  padding: 10px;
}
.ScanFriends ul:not(.ScanFriend) > li:first-of-type {
  display: grid;
  justify-items: center;
  align-items: center;
}
.ScanFriends .ScanFriend img {
  height: 100%;
  width: 100%;
}
.ScanFriend {
  position: relative;
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr 1fr;
}
.ScanFriend > li:first-of-type {
  grid-column: 1 / -1;
}
.ScanFriend button {
  width: 100%;
}
.ScanFriend .scanHolder > div {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 5px;
  pointer-events: initial;
  cursor: pointer;
}
.ScanFriend .scanHolder {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
